<template>
	<div>
	<div class="main-body" ref="wrapper"  :style="{ height: (wrapperHeight-59) + 'px' }">
		<slide-tab :tabList="tabList" class='tab'></slide-tab>
		<mt-loadmore  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :autoFill="isAutoFill">
			 <ul class="wrapper">
			 	<li v-for="(item,index) in satinList" :key="index">
			 		<div class="user-info clearfix">
			 			<div class="left header">
			 				<img :src="item.header" alt="">
			 			</div>
			 			<div class="left name-time">
			 				<div class="username">
			 					{{item.username}}
			 				</div>
			 				<div class="time">
			 					{{$moment().format("YYYY-MM-DD HH:MM:SS",item.passtime)}}
			 				</div>
			 			</div>
			 		</div>
			 		<div class="content">
			 			<div class="text" v-if="item.type == 'text'">
			 				{{item.text}}
			 			</div>
			 			<div class="gif" v-if="item.type == 'gif'">
			 				<div class="text">
			 					{{item.text}}
			 				</div>
			 				<div class="gif-img">
			 					<img :src="item.gif" alt="">
			 				</div>
			 		
			 			</div>
			 			<div class="image" v-if="item.type == 'image'">
			 				<div class="text">
			 					{{item.text}}
			 				</div>
			 				<div class="image-img">
			 			 		<img  :src="item.image" alt="">	
			 				</div>
			 			</div>
			 			<div class="video" v-if="item.type == 'video'">
			 				<div class="text">
			 					{{item.text}}
			 				</div>
					        <video id="myvideo" :src="item.video" :poster="item.thumbnail" :muted="muteStatus" :autoplay="isPlay" height="100%" width="100%">
					        </video>
					        <span class="ico ico-sound" :class="{ active: isMute }" v-on:click="closeSoundClick()"></span>
					        <span class="ico ico-skip"></span>
					        <span class="ico ico-video" :class="{ hide: isPlay }" v-on:click="playClick()"></span>
					    </div>
			 		</div>
			 		<div class="operation clearfix">
							<div>
								<i class="icon iconfont icon-dianzan"></i>
								{{item.up}}
							</div>
							<div>
								<i class="icon iconfont icon-zuji"></i>
								{{item.down}}
							</div>
							<div>
								<i class="icon iconfont icon-fenxiang"></i>
								分享
							</div>
							<div>
								<i class="icon iconfont icon-31pinglun"></i>
								{{item.comment}}
							</div>
			 		</div>
			 	</li>
			 </ul>
		</mt-loadmore>
	</div>
	</div>
</template>
<script>
import eventVue from "@/assets/js/eventVue.js";
	export default{
		data(){
			return {
				page:1,
				type:1,
				tabList:[
					{id:1,title:'推荐'},
					{id:2,title:'文字'},
					{id:3,title:'图片'},
					{id:4,title:'gif'},
					{id:5,title:'视频'}
				],
				satinList:[],
				isPlay:false,
				isMute:true,
			   	playStatus:'',
            	muteStatus:'',
			      //可以进行上拉
			      allLoaded: false,
			      //是否自动触发上拉函数
			      isAutoFill: false,
			      wrapperHeight: 0,
			}
		},
		created(){
			this.AxiosSatin(this.type);
			eventVue.$on("tabId",(message)=>{
				this.type = message;
			})
		},
		 mounted() {
    // 父控件要加上高度，否则会出现上拉不动的情况
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top;
      console.log(this.$refs.wrapper.getBoundingClientRect().top);
      console.log(document.documentElement.clientHeight);
  },
		watch:{
			type(newV){
				this.AxiosSatin(newV);
			}
		},
		methods:{
			 // 上拉加载
		    loadBottom() {
		      this.loadMore();
		    },
		    // 下来刷新加载
			AxiosSatin(type){
				this.$axios.get(`https://www.apiopen.top/satinGodApi?type=${type}&page=${this.page}`)
				.then(res=>{
					console.log(res);
					this.satinList = res.data.data;
				})
				.catch(err=>{
					console.log(err);
				})
			},
			closeSoundClick(){

			},
			playClick(){

			},
					    // 加载更多
		    loadMore() {
		      this.$axios.get(`https://www.apiopen.top/satinGodApi?type=${this.type}&page=${this.page}`)
		        .then(response => {
		          // concat数组的追加
		          this.satinList = this.satinList.concat(response.data.data);
		          if (response.data.data.length <= 0) {
		            this.allLoaded = true; // 若数据已全部获取完毕
		          }
		          this.page++;
		          this.$refs.loadmore.onBottomLoaded();
		        })
		        .catch(error => {
		          console.log(error);
		          alert("网络错误，不能访问");
		        });
		    }
		}
	}
</script>
<style lang="less" scoped>
.main-body {
  /* 加上这个才会有当数据充满整个屏幕，可以进行上拉加载更多的操作 */
  overflow: scroll;
}
	.wrapper{
		padding:0 12px;
		li{
			margin-bottom: 10px;
			border-bottom: 1px solid #a7a7a7;
			padding-bottom: 10px; 
			.user-info{
				margin-bottom: 10px;
				.header{
					width: 50px;
					height: 50px;
					border-radius:50%;
					overflow: hidden;
				}
				.username{
					padding: 5px 0 0 10px; 
					font-size: 14px;
					font-weight: 700;
				}
				.time{
					padding: 5px 0 0 10px; 
					font-size: 12px;
					color: #a7a7a7;
				}
			}
			.text{
				font-size: 14px; 
				margin-bottom: 5px;
			}
			.gif{
				.gif-img{
					max-width: 100%;
					img{
						max-width: 100%;
					}
					
				}
			}
			.image{
				.image-img{
					max-width: 100%;
					img{
						max-width: 100%;
					}
					
				}
			}
			.operation{
				div{
					margin-top: 10px;
					float: left;
					width: 25%;
					text-align: center;
					font-size: 14px;
					color: #ccc;
					i{
						vertical-align: top;	
						color: #c0c0c0;
					}
				}
			}
		}
	}
</style>


